.eventlist-container {
    padding-top: 18%;
    width: 100%;
    position: relative;
    color: black;
}
.bgimg{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: fixed;
    z-index: -99;
    top:0;
}
.eventlist_depname {
    text-align: center;
    font-size: 2em;
    padding: 5vw;
    color: white;
    font-family: 'Cuprum', sans-serif;
}
.event_list {
    overflow-x: hidden;
    overflow-y: auto;
    padding: 1em 0.3em;
    padding-top: 10%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}
.event {
    text-align: center;
    background-color: #e5e5e5;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    padding: 5% 0;
    margin: 1%;
    margin-bottom: 2vw;
    border-radius: 5px;
    cursor: pointer;
}
.event_logo {
    display: none;
}
.event_name {
    padding: 2px 16px;
    font-family: 'Cuprum', sans-serif;
}
@media screen and (min-width:768px) {
    .eventlist-container {
        padding-top: 10%;
    }
    .eventlist_depname {
        font-size: 3em;
        padding-bottom: 0;
        padding-top: 0;
    }
    .event_list {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        height: auto;
    }
    .event {
        background-color: #00000052;
        padding: 0;
        -ms-flex: 1 1;
            flex: 1 1;
        width: 30vw;
        min-width: 30vw;
        max-width: 30vw;
        max-height: 30vw;
        -webkit-box-shadow: 0px 4px 8px 0px rgba(0,0,0,1);
                box-shadow: 0px 4px 8px 0px rgba(0,0,0,1);
    }
    .event:hover {
        -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,1);
                box-shadow: 0px 8px 16px 0px rgba(0,0,0,1);
    }
    .event_logo {
        display: inline-block;
        max-width: 100%;
        border-radius: 5px 5px 0 0;
        overflow: hidden;
        height: 80%;
    }
    .event_logo img {
        border-radius: 5px 5px 0 0;
        -o-object-fit: cover;
           object-fit: cover;
        height: 100%;
        position: relative;
        left: 50%;
        -webkit-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
                transform: translateX(-50%);
    }
    .event_name {
        color: white;
        font-size: 2.3vw;
    }
}


