.about-container {
    color: white;
    padding: 0 5%;
    font-family: 'Cuprum', sans-serif;
}
.bgimg {
    top:0;
    left:0;
}
.ritu-title {
    text-align: center;
    font-size: 2em;
    padding: 5%;
}
.ritu-desc {
    padding: 2% 0%;
    text-align: center;
    line-height: 30px;
    font-size: 1.5em;
}
.card-list {
    display: block;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.dev-title {
    font-size: 2em;
    padding-bottom: 0.5em;
}
.team{
    padding-top: 3em;
}
.card {
    margin: 2%;
    background-color: white;
    min-width: 10vw;
    -ms-flex: 1 1;
        flex: 1 1;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    border-radius: 5px;
    color: black;
}
.card a {
    text-decoration: none;
    color: black;
}
.card:hover {
    -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
.card img {
    width: 100%;
    border-radius: 5px 5px 0 0;
}
.team-profile {
    padding: 0 5px;
}
.team-profile h4 {
    margin: 0;
}
@media screen and (min-width:768px){
    .ritu-desc{
        text-align: justify;
    }
    .about-container {
        padding: 0 15%;
    }
    .card-list {
        display: -ms-flexbox;
        display: flex;
    }
    .card {
        max-width: 10vw;
    }
}


