:root {
  --yellow:#e9b004;
  --red:#d42231;
  --blue:#0395d9;
  --green:#0fd502;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100vw;
  height: 100vh;
  min-width: 320px;
}
#root {
  width: 100%;
  height: 100%;
}
.canvas {
  top:0;
  left: 0;
  z-index: -2;
  position: fixed;
  height: 100vh;
  width: 100vw;
}



